/**
 * Zendesk API service for creating tickets and uploading attachments
 */

// Set to true to use local CORS proxy for development (Outlook Web)
// Set to false for Outlook Desktop (no CORS issues)
const USE_PROXY = true;
const PROXY_URL = "https://06zyva2dth.execute-api.ap-northeast-1.amazonaws.com";

/**
 * Create a Zendesk ticket
 * @param {Object} credentials - Zendesk credentials
 * @param {Object} ticketData - Ticket data
 * @returns {Promise<Object>} Created ticket response
 */
async function createTicket(credentials, ticketData) {
  const { subdomain, email, apiToken, groupId } = credentials;
  const { subject, body, requesterEmail, requesterName, attachmentTokens } =
    ticketData;

  // Build the API URL
  const apiUrl = `https://${subdomain}/api/v2/tickets.json`;

  // Prepare the ticket payload
  const payload = {
    ticket: {
      subject: subject,
      comment: {
        body: body,
        html_body: body,
        public: false,
      },
      requester: {
        email: requesterEmail,
        name: requesterName,
      },
    },
  };

  // Add group assignment if configured
  if (groupId) {
    payload.ticket.group_id = parseInt(groupId, 10);
  }

  // Add attachment tokens if present
  if (attachmentTokens && attachmentTokens.length > 0) {
    payload.ticket.comment.uploads = attachmentTokens;
  }

  // Create Basic Auth header
  const authString = btoa(`${email}/token:${apiToken}`);

  try {
    const fetchOptions = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Basic ${authString}`,
      },
      body: JSON.stringify(payload),
    };

    // Use proxy if enabled (for Outlook Web CORS workaround)
    let fetchUrl = apiUrl;
    if (USE_PROXY) {
      fetchUrl = PROXY_URL;
      fetchOptions.headers["X-Target-URL"] = apiUrl;
    }

    const response = await fetch(fetchUrl, fetchOptions);

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`Zendesk API error (${response.status}): ${errorText}`);
    }

    const result = await response.json();
    return result;
  } catch (error) {
    console.error("Error creating Zendesk ticket:", error);
    throw error;
  }
}

/**
 * Add a comment to an existing Zendesk ticket
 * @param {Object} credentials - Zendesk credentials
 * @param {number} ticketId - Existing ticket ID
 * @param {Object} commentData - Comment data
 * @returns {Promise<Object>} Updated ticket response
 */
async function addCommentToTicket(credentials, ticketId, commentData) {
  const { subdomain, email, apiToken } = credentials;
  const { body, attachmentTokens } = commentData;

  // Build the API URL
  const apiUrl = `https://${subdomain}/api/v2/tickets/${ticketId}.json`;

  // Prepare the update payload
  const payload = {
    ticket: {
      comment: {
        body: body,
        html_body: body,
        public: false, // Internal note
      },
    },
  };

  // Add attachment tokens if present
  if (attachmentTokens && attachmentTokens.length > 0) {
    payload.ticket.comment.uploads = attachmentTokens;
  }

  // Create Basic Auth header
  const authString = btoa(`${email}/token:${apiToken}`);

  try {
    const fetchOptions = {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Basic ${authString}`,
      },
      body: JSON.stringify(payload),
    };

    // Use proxy if enabled (for Outlook Web CORS workaround)
    let fetchUrl = apiUrl;
    if (USE_PROXY) {
      fetchUrl = PROXY_URL;
      fetchOptions.headers["X-Target-URL"] = apiUrl;
    }

    const response = await fetch(fetchUrl, fetchOptions);

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`Zendesk API error (${response.status}): ${errorText}`);
    }

    const result = await response.json();
    return result;
  } catch (error) {
    console.error("Error adding comment to Zendesk ticket:", error);
    throw error;
  }
}

/**
 * Upload an attachment to Zendesk
 * @param {Object} credentials - Zendesk credentials
 * @param {Object} attachment - Attachment data
 * @returns {Promise<string>} Upload token
 */
async function uploadAttachment(credentials, attachment) {
  const { subdomain, email, apiToken } = credentials;
  const { filename, content } = attachment;

  // Build the API URL with filename
  const apiUrl = `https://${subdomain}/api/v2/uploads.json?filename=${encodeURIComponent(
    filename,
  )}`;

  // Create Basic Auth header
  const authString = btoa(`${email}/token:${apiToken}`);

  try {
    // Convert base64 to binary if needed
    let binaryContent;
    if (typeof content === "string") {
      // Assume base64 encoded
      const binaryString = atob(content);
      const bytes = new Uint8Array(binaryString.length);
      for (let i = 0; i < binaryString.length; i++) {
        bytes[i] = binaryString.charCodeAt(i);
      }
      binaryContent = bytes;
    } else {
      binaryContent = content;
    }

    const fetchOptions = {
      method: "POST",
      headers: {
        Authorization: `Basic ${authString}`,
        "Content-Type": "application/binary",
      },
      body: binaryContent,
    };

    // Use proxy if enabled (for Outlook Web CORS workaround)
    let fetchUrl = apiUrl;
    if (USE_PROXY) {
      fetchUrl = PROXY_URL;
      fetchOptions.headers["X-Target-URL"] = apiUrl;
    }

    const response = await fetch(fetchUrl, fetchOptions);

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(
        `Failed to upload attachment (${response.status}): ${errorText}`,
      );
    }

    const result = await response.json();
    return result.upload.token;
  } catch (error) {
    console.error("Error uploading attachment:", error);
    throw error;
  }
}

/**
 * Test Zendesk connection
 * @param {Object} credentials - Zendesk credentials
 * @returns {Promise<boolean>} True if connection is successful
 */
async function testConnection(credentials) {
  const { subdomain, email, apiToken } = credentials;

  // Note: For development, CORS will block direct API calls from localhost
  // In production, use a backend proxy or Outlook Desktop (which doesn't have CORS restrictions)
  const apiUrl = `https://${subdomain}/api/v2/users/me.json`;

  const authString = btoa(`${email}/token:${apiToken}`);

  try {
    const fetchOptions = {
      method: "GET",
      headers: {
        Authorization: `Basic ${authString}`,
      },
    };

    // Use proxy if enabled (for Outlook Web CORS workaround)
    let fetchUrl = apiUrl;
    if (USE_PROXY) {
      fetchUrl = PROXY_URL;
      fetchOptions.headers["X-Target-URL"] = apiUrl;
    }

    const response = await fetch(fetchUrl, fetchOptions);

    return response.ok;
  } catch (error) {
    console.error("Error testing Zendesk connection:", error);
    return false;
  }
}

/**
 * Fetch all groups from Zendesk
 * @param {Object} credentials - Zendesk credentials
 * @returns {Promise<Array>} Array of groups
 */
async function fetchGroups(credentials) {
  const { subdomain, email, apiToken } = credentials;

  const apiUrl = `https://${subdomain}/api/v2/groups.json`;
  const authString = btoa(`${email}/token:${apiToken}`);

  try {
    const fetchOptions = {
      method: "GET",
      headers: {
        Authorization: `Basic ${authString}`,
      },
    };

    // Use proxy if enabled (for Outlook Web CORS workaround)
    let fetchUrl = apiUrl;
    if (USE_PROXY) {
      fetchUrl = PROXY_URL;
      fetchOptions.headers["X-Target-URL"] = apiUrl;
    }

    const response = await fetch(fetchUrl, fetchOptions);

    if (!response.ok) {
      throw new Error(`Failed to fetch groups: ${response.status}`);
    }

    const result = await response.json();
    return result.groups || [];
  } catch (error) {
    console.error("Error fetching Zendesk groups:", error);
    throw error;
  }
}

// Export for use in other modules
if (typeof module !== "undefined" && module.exports) {
  module.exports = {
    createTicket,
    addCommentToTicket,
    uploadAttachment,
    testConnection,
    fetchGroups,
  };
}
