/**
 * Storage utility for managing Zendesk credentials using Office.RoamingSettings
 */

const STORAGE_KEYS = {
  ZENDESK_SUBDOMAIN: "zendeskSubdomain",
  ZENDESK_EMAIL: "zendeskEmail",
  ZENDESK_API_TOKEN: "zendeskApiToken",
  TICKET_SUBJECT_TEMPLATE: "ticketSubjectTemplate",
  ADD_IN_ENABLED: "addInEnabled",
  CONVERSATION_TICKET_MAP: "conversationTicketMap",
  ZENDESK_GROUP_ID: "zendeskGroupId",
};

/**
 * Save settings to Office roaming settings
 * @param {Object} settings - Settings object containing credentials
 * @returns {Promise<void>}
 */
async function saveSettings(settings) {
  return new Promise((resolve, reject) => {
    Office.context.roamingSettings.set(
      STORAGE_KEYS.ZENDESK_SUBDOMAIN,
      settings.subdomain || ""
    );
    Office.context.roamingSettings.set(
      STORAGE_KEYS.ZENDESK_EMAIL,
      settings.email || ""
    );
    Office.context.roamingSettings.set(
      STORAGE_KEYS.ZENDESK_API_TOKEN,
      settings.apiToken || ""
    );
    Office.context.roamingSettings.set(
      STORAGE_KEYS.TICKET_SUBJECT_TEMPLATE,
      settings.subjectTemplate || "Support Request from Outlook"
    );
    Office.context.roamingSettings.set(
      STORAGE_KEYS.ADD_IN_ENABLED,
      settings.enabled !== false
    );
    Office.context.roamingSettings.set(
      STORAGE_KEYS.ZENDESK_GROUP_ID,
      settings.groupId || ""
    );

    Office.context.roamingSettings.saveAsync((result) => {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        resolve();
      } else {
        reject(new Error("Failed to save settings: " + result.error.message));
      }
    });
  });
}

/**
 * Load settings from Office roaming settings
 * @returns {Promise<Object>} Settings object
 */
async function loadSettings() {
  return new Promise((resolve) => {
    const settings = {
      subdomain:
        Office.context.roamingSettings.get(STORAGE_KEYS.ZENDESK_SUBDOMAIN) ||
        "",
      email:
        Office.context.roamingSettings.get(STORAGE_KEYS.ZENDESK_EMAIL) || "",
      apiToken:
        Office.context.roamingSettings.get(STORAGE_KEYS.ZENDESK_API_TOKEN) ||
        "",
      subjectTemplate:
        Office.context.roamingSettings.get(
          STORAGE_KEYS.TICKET_SUBJECT_TEMPLATE
        ) || "Support Request from Outlook",
      enabled:
        Office.context.roamingSettings.get(STORAGE_KEYS.ADD_IN_ENABLED) !==
        false,
      groupId:
        Office.context.roamingSettings.get(STORAGE_KEYS.ZENDESK_GROUP_ID) || "",
    };
    resolve(settings);
  });
}

/**
 * Check if credentials are configured
 * @returns {Promise<boolean>}
 */
async function isConfigured() {
  const settings = await loadSettings();
  return !!(settings.subdomain && settings.email && settings.apiToken);
}

/**
 * Clear all settings
 * @returns {Promise<void>}
 */
async function clearSettings() {
  return new Promise((resolve, reject) => {
    Office.context.roamingSettings.remove(STORAGE_KEYS.ZENDESK_SUBDOMAIN);
    Office.context.roamingSettings.remove(STORAGE_KEYS.ZENDESK_EMAIL);
    Office.context.roamingSettings.remove(STORAGE_KEYS.ZENDESK_API_TOKEN);
    Office.context.roamingSettings.remove(STORAGE_KEYS.TICKET_SUBJECT_TEMPLATE);
    Office.context.roamingSettings.remove(STORAGE_KEYS.ADD_IN_ENABLED);

    Office.context.roamingSettings.saveAsync((result) => {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        resolve();
      } else {
        reject(new Error("Failed to clear settings: " + result.error.message));
      }
    });
  });
}

/**
 * Clear conversation-ticket mappings
 * @returns {Promise<void>}
 */
async function clearConversationMappings() {
  return new Promise((resolve, reject) => {
    Office.context.roamingSettings.remove(STORAGE_KEYS.CONVERSATION_TICKET_MAP);

    Office.context.roamingSettings.saveAsync((result) => {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        resolve();
      } else {
        reject(new Error("Failed to clear mappings: " + result.error.message));
      }
    });
  });
}

/**
 * Save conversation to ticket mapping
 * @param {string} conversationId - Outlook conversation ID
 * @param {number} ticketId - Zendesk ticket ID
 * @returns {Promise<void>}
 */
async function saveConversationTicketMapping(conversationId, ticketId) {
  return new Promise((resolve, reject) => {
    // Get existing mappings
    const mappings = Office.context.roamingSettings.get(STORAGE_KEYS.CONVERSATION_TICKET_MAP) || {};

    // Add new mapping
    mappings[conversationId] = ticketId;

    // Save back
    Office.context.roamingSettings.set(STORAGE_KEYS.CONVERSATION_TICKET_MAP, mappings);

    Office.context.roamingSettings.saveAsync((result) => {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        resolve();
      } else {
        reject(new Error("Failed to save conversation mapping: " + result.error.message));
      }
    });
  });
}

/**
 * Get ticket ID for a conversation
 * @param {string} conversationId - Outlook conversation ID
 * @returns {Promise<number|null>} Zendesk ticket ID or null if not found
 */
async function getTicketForConversation(conversationId) {
  return new Promise((resolve) => {
    const mappings = Office.context.roamingSettings.get(STORAGE_KEYS.CONVERSATION_TICKET_MAP) || {};
    resolve(mappings[conversationId] || null);
  });
}

// Export for use in other modules
if (typeof module !== "undefined" && module.exports) {
  module.exports = {
    saveSettings,
    loadSettings,
    isConfigured,
    clearSettings,
    saveConversationTicketMapping,
    getTicketForConversation,
    clearConversationMappings,
  };
}
