/* global Office */

// Import utilities
const storage = require('../utils/storage');
const zendesk = require('../services/zendesk');

Office.onReady((info) => {
  if (info.host === Office.HostType.Outlook) {
    document.getElementById('saveButton').onclick = saveConfiguration;
    document.getElementById('testButton').onclick = testConnection;
    document.getElementById('clearMappingsButton').onclick = clearMappings;
    document.getElementById('loadGroupsButton').onclick = loadGroups;

    // Load existing settings
    loadConfiguration();
  }
});

/**
 * Load existing configuration into the form
 */
async function loadConfiguration() {
  try {
    const settings = await storage.loadSettings();

    document.getElementById('subdomain').value = settings.subdomain || '';
    document.getElementById('email').value = settings.email || '';
    document.getElementById('apiToken').value = settings.apiToken || '';
    document.getElementById('subjectTemplate').value = settings.subjectTemplate || 'Support Request from Outlook';
    document.getElementById('enabled').checked = settings.enabled !== false;

    // Set the selected group if we have one saved
    if (settings.groupId) {
      const groupSelect = document.getElementById('groupId');
      // Check if this option exists, if not add it temporarily
      let optionExists = false;
      for (let i = 0; i < groupSelect.options.length; i++) {
        if (groupSelect.options[i].value === settings.groupId) {
          optionExists = true;
          break;
        }
      }
      if (!optionExists) {
        // Add a temporary option with just the ID
        const tempOption = document.createElement('option');
        tempOption.value = settings.groupId;
        tempOption.textContent = `Group ID: ${settings.groupId}`;
        groupSelect.appendChild(tempOption);
      }
      groupSelect.value = settings.groupId;
    }

    if (settings.subdomain && settings.email && settings.apiToken) {
      showStatus('Configuration loaded successfully', 'success');
    }
  } catch (error) {
    console.error('Error loading configuration:', error);
    showStatus('Error loading configuration: ' + error.message, 'error');
  }
}

/**
 * Save configuration to storage
 */
async function saveConfiguration() {
  const saveButton = document.getElementById('saveButton');
  const originalText = saveButton.textContent;

  try {
    // Disable button during save
    saveButton.disabled = true;
    saveButton.textContent = 'Saving...';

    // Get form values
    const subdomain = document.getElementById('subdomain').value.trim();
    const email = document.getElementById('email').value.trim();
    const apiToken = document.getElementById('apiToken').value.trim();
    const subjectTemplate = document.getElementById('subjectTemplate').value.trim() || 'Support Request from Outlook';
    const enabled = document.getElementById('enabled').checked;
    const groupId = document.getElementById('groupId').value.trim();

    // Validate required fields
    if (!subdomain || !email || !apiToken) {
      showStatus('Please fill in all required fields', 'error');
      return;
    }

    // Validate subdomain format
    if (!subdomain.includes('.zendesk.com')) {
      showStatus('Subdomain should be in format: yourcompany.zendesk.com', 'error');
      return;
    }

    // Save settings
    await storage.saveSettings({
      subdomain,
      email,
      apiToken,
      subjectTemplate,
      enabled,
      groupId
    });

    showStatus('Configuration saved successfully!', 'success');
  } catch (error) {
    console.error('Error saving configuration:', error);
    showStatus('Error saving configuration: ' + error.message, 'error');
  } finally {
    saveButton.disabled = false;
    saveButton.textContent = originalText;
  }
}

/**
 * Test Zendesk connection
 */
async function testConnection() {
  const testButton = document.getElementById('testButton');
  const originalText = testButton.textContent;

  try {
    // Disable button during test
    testButton.disabled = true;
    testButton.textContent = 'Testing...';

    // Get credentials
    const subdomain = document.getElementById('subdomain').value.trim();
    const email = document.getElementById('email').value.trim();
    const apiToken = document.getElementById('apiToken').value.trim();

    // Validate required fields
    if (!subdomain || !email || !apiToken) {
      showStatus('Please fill in all fields before testing', 'error');
      return;
    }

    // Validate subdomain format
    if (!subdomain.includes('.zendesk.com')) {
      showStatus('Subdomain should be in format: yourcompany.zendesk.com', 'error');
      return;
    }

    // Test connection
    const isValid = await zendesk.testConnection({ subdomain, email, apiToken });

    if (isValid) {
      showStatus('Connection successful! Your credentials are valid.', 'success');

      // Auto-load groups after successful connection test
      setTimeout(() => {
        loadGroups();
      }, 500);
    } else {
      showStatus('Connection failed. Please check your credentials.', 'error');
    }
  } catch (error) {
    console.error('Error testing connection:', error);
    showStatus('Connection test failed: ' + error.message, 'error');
  } finally {
    testButton.disabled = false;
    testButton.textContent = originalText;
  }
}

/**
 * Clear conversation-ticket mappings
 */
async function clearMappings() {
  const clearButton = document.getElementById('clearMappingsButton');
  const originalText = clearButton.textContent;

  try {
    clearButton.disabled = true;
    clearButton.textContent = 'Clearing...';

    await storage.clearConversationMappings();

    showStatus('Thread mappings cleared successfully! New emails will create new tickets.', 'success');
  } catch (error) {
    console.error('Error clearing mappings:', error);
    showStatus('Error clearing mappings: ' + error.message, 'error');
  } finally {
    clearButton.disabled = false;
    clearButton.textContent = originalText;
  }
}

/**
 * Load groups from Zendesk and populate dropdown
 */
async function loadGroups() {
  const loadButton = document.getElementById('loadGroupsButton');
  const originalText = loadButton.textContent;
  const groupSelect = document.getElementById('groupId');

  try {
    loadButton.disabled = true;
    loadButton.textContent = 'Loading...';
    groupSelect.disabled = true;

    // Get credentials
    const subdomain = document.getElementById('subdomain').value.trim();
    const email = document.getElementById('email').value.trim();
    const apiToken = document.getElementById('apiToken').value.trim();

    // Validate required fields
    if (!subdomain || !email || !apiToken) {
      showStatus('Please fill in credentials before loading groups', 'error');
      return;
    }

    // Fetch groups
    const groups = await zendesk.fetchGroups({ subdomain, email, apiToken });

    // Clear existing options (except the first "no group" option)
    groupSelect.innerHTML = '<option value="">-- No Group Assignment --</option>';

    // Add groups to dropdown
    if (groups.length === 0) {
      showStatus('No groups found in your Zendesk account', 'info');
    } else {
      groups.forEach(group => {
        const option = document.createElement('option');
        option.value = group.id;
        option.textContent = group.name;
        groupSelect.appendChild(option);
      });

      showStatus(`Loaded ${groups.length} group(s) successfully!`, 'success');
    }
  } catch (error) {
    console.error('Error loading groups:', error);
    showStatus('Failed to load groups: ' + error.message, 'error');
  } finally {
    loadButton.disabled = false;
    loadButton.textContent = originalText;
    groupSelect.disabled = false;
  }
}

/**
 * Show status message
 */
function showStatus(message, type) {
  const statusElement = document.getElementById('statusMessage');
  statusElement.textContent = message;
  statusElement.className = `status-message ${type}`;
  statusElement.classList.remove('hidden');

  // Auto-hide after 5 seconds
  setTimeout(() => {
    statusElement.classList.add('hidden');
  }, 5000);
}
